}

.question .answerContainer {
    list-style-type: none;
    display: flex;
    justify-content: space-between;
    padding: 0;
    margin: 0;
    flex-wrap: wrap;
    transition: opacity 1s ease-in-out;
    align-content: stretch
    /*margin-left: 90px;*/
}


.question .answer {
    width: 100%;
    transition: opacity 1s ease-in-out;
    margin-top: 20px;


}


.question .answer button, .button {
    text-transform: uppercase;
    color: #ffffff;
    width: 100%;
    font-size: inherit;
    padding: 20px;
    text-align: left;
    display: flex;
    align-items: center;
    border: none;
    background-color: rgba(255, 255, 255, 0.05);
    border-radius: 13px;
    cursor: pointer;
    transition: 0.5s;
    font-family: inherit;
    /* border: 1px solid transparent */
}
.question .answer button.withImage .image{
    height: 100px;
    margin-right: 15px;
}
.question .answer button.withImage .icon{
    display: none;
}


.question .answer button:hover,  .button:hover {

    transition: 0.5s ease-in-out;
    background-color: rgba(255, 255, 255, 0.15);
    box-shadow: 11px 12px 50px -11px rgba(0, 0, 0, 0.67);
    /* border: 1px solid #1de5ac */
}

