.modal{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    max-width: 400px;
    background-color: #000000;
    color: #ffffff;
    border: 2px solid #f5db00;
    border-radius: 25px;
    padding: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: NAMU-Pro;
    box-sizing: border-box;
}

.modal img{
    height: 50px;
    margin-bottom: 30px;
}

.modal .button{
    text-align: center;
    justify-content: center;
}

.loader {
    height: 100vh;
    width: 100%;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(0, 0, 0);
    backdrop-filter: blur(5px);
    opacity: 1;
    z-index: 20;
    top: 0;
    left: 0;
}

.nowrap{
    flex-wrap: nowrap !important;
}

.car__body {
    animation: shake 0.2s ease-in-out infinite alternate;
}

.carLine {
    transform-origin: center right;
    stroke-dasharray: 22;
    animation: line 0.8s ease-in-out infinite;
    animation-fill-mode: both;
}

.carLineTop {
    animation-delay: 0s;
}

.carLineMiddle {
    animation-delay: 0.2s;
}

.carLineBottom {
    animation-delay: 0.4s;
}


@keyframes shake {
    0% {
        transform: translateY(-1%);
    }

    100% {
        transform: translateY(3%);
    }
}

@keyframes line {
    0% {
        stroke-dashoffset: 22;
    }

    25% {
        stroke-dashoffset: 22;
    }

    50% {
        stroke-dashoffset: 0;
    }

    51% {
        stroke-dashoffset: 0;
    }

    80% {
        stroke-dashoffset: -22;
    }

    100% {
        stroke-dashoffset: -22;
    }
}

.finish{
    background-color: #18a980 !important ;
}


.resultText img{
